// Matches strings of plain ASCII letters and digits, hyphens and underscores.
export const allowedUsernameCharacters = (value        )          => /^[\w-]*$/.test(value)

// Matches non-empty strings of plain ASCII letters and digits.
export const alphanumeric = (value        )          => /^[A-Za-z\d]+$/.test(value)

export const noConsecutiveHyphensOrUnderscores = (value        )          => !value.includes('--') && !value.includes('__')

export const noLeadingOrTrailingHyphen = (value        )          => !value.startsWith('-') && !value.endsWith('-')
export const noLeadingOrTrailingUnderscore = (value        )          => !value.startsWith('_') && !value.endsWith('_')

export const decimals = (digits        )                               => (value        ) => Number.isInteger(value * Math.pow(10, digits))

export const noUppercase = (value        )          => value.toLowerCase() === value

export const noWhitespace = (value        )          => /^\S+$/.test(value)
